import React from 'react';
import {Helmet} from "react-helmet";

const about = () => {
    return (
        <React.Fragment>
            <Helmet>
                <title>The Dining Room | About project</title>
                <meta name="keywords" 
                        content="menu, founded"
                />
                <meta
                    name="description"
                    content="The Dining Room was founded in 2016 & holds information about local restaurants"
                />
            </Helmet>
            <div className="about-route">
                <h1>About Us</h1>
                <p><em>The Dining Room</em> was first founded in the late spring of 2016 when two boys were really hungry and didn't know where to go to eat. Faced with so many choices they decided to build a website which will hold information of all the local restaurants, their working hours and their menus.</p>
            </div>
        </React.Fragment>
    );
}

export default about;